import _get from 'lodash/get';
import { ELEMENTS_TYPE, SURVEYJS, ELEMENTS_TITLE } from '../../Constants.js';

const htmlTemplate = `<div>
      <input class='text_inputer form2 required' type='date' style='display: block;'></input>
    </div>`;

export const DatetimePicker = Survey => {
  const widgetType = ELEMENTS_TYPE.datetimePicker;
  const widget = {
    name: widgetType,
    title: ELEMENTS_TITLE.datetimePicker,
    iconName: '',
    htmlTemplate,
    widgetIsLoaded: () => {
      return true;
    },
    isFit: question => {
      return question.getType() === widgetType;
    },
    activatedByChanged: activatedBy => {
      const metaData = _get(Survey, SURVEYJS.metaData);
      metaData.addClass(widgetType, [], null, 'empty');
    },
    afterRender: (question, rootElement) => {
      const [element] = rootElement.getElementsByTagName('input');
      element.onchange = () => {
        const elementValue = _get(element, 'value', '');
        question.value = elementValue;
      };
      const onValueChangedCallback = () => {
        const questionValue = _get(question, 'value', '');
        element.value = questionValue;
      };
      const onReadOnlyChangedCallback = () => {
        if (question.isReadOnly) {
          return element.setAttribute('disabled', 'true');
        } else {
          return element.removeAttribute('disabled');
        }
      };
      question.readOnlyChangedCallback = onReadOnlyChangedCallback;
      question.valueChangedCallback = onValueChangedCallback;
      (function initial() {
        onReadOnlyChangedCallback();
        onValueChangedCallback();
      })();
    },
    willUnmount: (question, el) => {
      question.value = undefined;
    },
  };

  const Instance = _get(Survey, SURVEYJS.widgetCollectionInstance);
  Instance.addCustomWidget(widget, SURVEYJS.customType);
};
